"use client";

import React, { useState, useEffect } from "react";
import { useSearchParams, useRouter } from "next/navigation";
import AddonServices from "../../components/AddonServices";

export default function CheckoutPage() {
  const searchParams = useSearchParams();
  const router = useRouter();

  const domain = searchParams.get("domain") || "";
  const voucher = searchParams.get("voucher") || "";

  const [selectedAddon, setSelectedAddon] = useState("none");
  const [discountApplied, setDiscountApplied] = useState(false);

  // Prices
  const domainPrice = 10; // base price for domain registration
  const addonPrices: Record<string, number> = {
    none: 0,
    shared: 5,
    vps: 20,
    wordpress: 15,
  };

  const [totalPrice, setTotalPrice] = useState(domainPrice);

  useEffect(() => {
    const addonPrice = addonPrices[selectedAddon] || 0;
    let price = domainPrice + addonPrice;

    // Apply 10% discount if voucher exists
    if (voucher) {
      price = price * 0.9;
      setDiscountApplied(true);
    } else {
      setDiscountApplied(false);
    }

    setTotalPrice(price);
  }, [selectedAddon, voucher]);

  const handleAddonSelect = (addonId: string) => {
    setSelectedAddon(addonId);
  };

  const handleCheckout = () => {
    alert(
      `Thank you for registering ${domain} with ${
        selectedAddon === "none" ? "no add-ons" : selectedAddon
      }. Total price: $${totalPrice.toFixed(2)}`
    );
    // Clear voucher and domain from localStorage
    localStorage.removeItem("domainVoucher");
    localStorage.removeItem("searchedDomain");
    router.push("/");
  };

  return (
    <main className="min-h-screen bg-gradient-to-r from-gray-900 via-gray-800 to-gray-900 text-white px-4 py-12 flex flex-col items-center">
      <section className="max-w-3xl w-full bg-gray-900 rounded-lg p-8">
        <h1 className="text-3xl font-bold mb-6 text-center">Checkout</h1>
        <div className="mb-6">
          <label className="block mb-2 font-semibold">Domain</label>
          <input
            type="text"
            value={domain}
            readOnly
            className="w-full rounded-md border border-gray-600 bg-gray-800 px-4 py-3 text-white"
          />
        </div>
        <AddonServices onSelect={handleAddonSelect} />
        {voucher && (
          <div className="mt-6 p-4 bg-green-700 rounded-md text-center font-semibold">
            Voucher <span className="font-mono">{voucher}</span> applied! 10% discount
            included.
          </div>
        )}
        <div className="mt-8 text-right text-xl font-bold">
          Total: ${totalPrice.toFixed(2)}
        </div>
        <button
          onClick={handleCheckout}
          className="mt-6 w-full bg-blue-600 hover:bg-blue-700 py-3 rounded-md font-semibold transition"
        >
          Complete Registration
        </button>
      </section>
    </main>
  );
}
