"use client";

import React, { useState } from "react";

interface AddonServicesProps {
  onSelect: (selected: string) => void;
}

const addons = [
  { id: "none", label: "None", price: 0 },
  { id: "shared", label: "Shared Hosting", price: 5 },
  { id: "vps", label: "VPS Hosting", price: 20 },
  { id: "wordpress", label: "WordPress Hosting", price: 15 },
];

const AddonServices: React.FC<AddonServicesProps> = ({ onSelect }) => {
  const [selectedAddon, setSelectedAddon] = useState("none");

  const handleSelect = (id: string) => {
    setSelectedAddon(id);
    onSelect(id);
  };

  return (
    <div className="max-w-md mx-auto mt-8">
      <h3 className="text-xl font-semibold mb-4 text-white">Add-on Services</h3>
      <div className="flex flex-col space-y-4">
        {addons.map((addon) => (
          <label
            key={addon.id}
            className={`flex items-center justify-between cursor-pointer rounded-md border border-gray-600 px-4 py-3 transition ${
              selectedAddon === addon.id ? "bg-blue-600 border-blue-600" : "bg-gray-900"
            }`}
          >
            <div className="flex items-center space-x-3">
              <input
                type="radio"
                name="addon"
                value={addon.id}
                checked={selectedAddon === addon.id}
                onChange={() => handleSelect(addon.id)}
                className="accent-blue-500"
              />
              <span className="text-white font-medium">{addon.label}</span>
            </div>
            <span className="text-white font-semibold">${addon.price}</span>
          </label>
        ))}
      </div>
    </div>
  );
};

export default AddonServices;
